import { useState, useEffect } from 'react'
import { ethers } from 'ethers'
import { Row, Col, Card, Button } from 'react-bootstrap'


export const Home = ({ marketplace, nft }) => {
  console.log("constructing home")
  const [items, setItems] = useState([])
  const [loading, setLoading] = useState(true)

  const loadMarketplaceItems = async () => {
    console.log("loadMarketplaceItems ")
    const itemCount = await marketplace.itemCount()
    console.log(`fetch items '${itemCount}' from the marketplace`)
    
    let items = []
    if(itemCount > 0) {
      for(let i = 0; i <= itemCount; i++) {
        console.log("round: ", i)
        const item = await marketplace.items(i)

        if(item.sold) {

        } else {
          const uri = await nft.tokenURI(item.tokenId)
          const response = await fetch(uri)
          const metadata = await response.json()
          const totalPrice = await marketplace.getTotalPrice(item.itemId)

          items.push({
            totalPrice,
            itemId: item.itemId,
            seller: item.seller,
            name: metadata.name,
            description: metadata.description,
            image: metadata.image
          })
        }
      }
    }

    console.log("set data")
    setItems(items)
    setLoading(false)    
  }

  const buyMarketItem = async(item) => {
    console.log("buyMarketItem")
    await (await marketplace.purchaseItem(item.itemId, { value: item.totalPrice })).wait()
    loadMarketplaceItems()
  }

  useEffect(() => {
    loadMarketplaceItems()
  }, [])

  if(loading) {
    return (
      <main style={{ padding: '1rem 0' }}>
        <h2>Loading...</h2>
      </main>
    )
  }

  return (
    <div
      className="flex justify center">
        {items.length > 0 ? (
          <div className="px-5 container">
            <Row xs={1} md={2} lg={4} className="g-4 py-5">
              {items.map((item, index) => (
                <Col key={index} className="overflow-hidden">
                  <Card>
                    <Card.Img variant="top" src={item.image} />
                    <Card.Body color="secondary">
                      <Card.Title>{item.name}</Card.Title>
                      <Card.Description>{item.description}</Card.Description>
                    </Card.Body>
                    <Card.Footer>
                      <div className='d-grid'>
                        <Button onClick={() => buyMarketItem(item)} variant="primary" size="lg">
                          Buy for {ethers.utils.formatEther(item.totalPrice)} ETH
                        </Button>
                      </div>
                    </Card.Footer>
                  </Card>
                </Col>
              ))}
            </Row>
          </div>
          ) : (<main>
            <h2>No listed assets</h2>
          </main>)}
    </div>
  )
}